/* Container */

.container {
  max-width: 960px;
}

/* Width */

.w-25 {
  width: 25%;
}

.w-50 {
  width: 50%;
}

.w-75 {
  width: 75%;
}

/* Padding */

.p-4 {
  padding: 4px;
}

.p-16 {
  padding: 16px;
}

.pr-8 {
  padding-right: 8px;
}

.px-16 {
  padding-left: 16px;
  padding-right: 16px;
}

.py-32 {
  padding-top: 32px;
  padding-bottom: 32px;
}

/* Margin */

.mb-8 {
  margin-bottom: 8px;
}

.mb-16 {
  margin-bottom: 16px;
}

.mb-32 {
  margin-bottom: 32px;
}

.mr-4 {
  margin-right: 4px;
}

.ml-4 {
  margin-left: 4px;
}

.my-64 {
  margin-top: 64px;
  margin-bottom: 64px;
}

.mx-auto {
  margin-left: auto;
  margin-right: auto;
}

/* Text */

.ta-center {
  text-align: center;
}

.ta-right {
  text-align: right;
}

.tt-capitalize {
  text-transform: capitalize;
}

/* Font */

.fs-24 {
  font-size: 24px;
}

.fs-32 {
  font-size: 32px;
}

.fw-bold {
  font-weight: bold;
}

.ff-arial {
  font-family: Arial, Helvetica, sans-serif;
}

.ff-times {
  font-family: 'Times New Roman', Times, serif;
}

.fs-italic {
  font-style: italic;
}

/* Border */

.bt-1 {
  border-top-width: 1px;
}

.bt-solid {
  border-top-style: solid;
}

.bb-1 {
  border-bottom-width: 1px;
}

.bb-solid {
  border-bottom-style: solid;
}

/* Display */

.d-flex {
  display: flex;
}

.jc-center {
  justify-content: center;
}

.fw-wrap {
  flex-wrap: wrap;
}

/* small */

@media (max-width: 640px) {
  .w-s-100 {
    width: 100%;
  }
}

